.SuperCheckbox_checkbox__199QL
{
    font-size: 18px;

    position: relative;

    display: block;

    width: 93px;
    margin-bottom: 20px;
    padding-right: 36px;

    cursor: pointer;

    color: #bdc3c7;
}

.SuperCheckbox_checkbox__199QL input
{
    position: absolute;

    opacity: 0;
}

.SuperCheckbox_checkbox__199QL .SuperCheckbox_check__29Xu6
{
    position: absolute;
    top: -2px;
    right: 0;

    width: 20px;
    height: 20px;

    transition: .7s;
    text-align: center;

    color: #2c3e50;
    border: 1px solid #fff;
    border-radius: 4px;
    background: #fff;
}

.SuperCheckbox_checkbox__199QL .SuperCheckbox_check__29Xu6:focus
{
    outline: none;
}

.SuperCheckbox_checkbox__199QL .SuperCheckbox_check__29Xu6:before
{
    position: absolute;
    top: 3px;
    left: 3px;

    display: none;

    width: 11px;
    height: 6px;

    content: '';
    cursor: pointer;
    transform: rotate(-45deg);

    border-bottom: 3px solid #fff;
    border-left: 3px solid #fff;
}

.SuperCheckbox_checkbox__199QL:hover > .SuperCheckbox_check__29Xu6
{
    box-shadow: -1px -1px 20px 1px rgba(45, 226, 230, .3), 1px 1px 20px 1px rgba(45, 226, 230, .3);
}

.SuperCheckbox_checkbox__199QL input:focus ~ .SuperCheckbox_check__29Xu6
{
    box-shadow: -1px -1px 20px 1px #ff3864, 1px 1px 20px 1px #ff3864;
}

.SuperCheckbox_checkbox__199QL input:checked ~ .SuperCheckbox_check__29Xu6
{
    background: #ff3864;
}

.SuperCheckbox_checkbox__199QL input:checked ~ .SuperCheckbox_check__29Xu6:before
{
    display: block;
}

